.image{
    display: flex;
    flex-direction: column;
    align-items:center;
    text-align: justify;
    max-height:250px;
    min-width:100px;
    margin:5px;
    width:100%;
    font-style: italic;
}
.image_image{
    height: 200px;
    width: 200px;
    border-radius: 50%;
}
.image_image:hover{
    height: 220px;
    width: 220px;
    box-shadow: 2px 2px 6px;
}
.image_title{
    margin-top: 10px;
    font-size: 18px;
}