    align-items: center;
}
.resume{
    font-family:inherit;
    font-size: 25px;
    text-decoration: underline;
}
.name{
    font-size: 20px;
    padding:none;
}
.download{
    margin-top: 3%;
    width:40%;
    color:black;
    background-color:  rgba(180, 201, 240, 0.979);
